body{
  
  background-color: black;
  height: 100%;
}
.App {
  text-align: center;
  
}
p{
  font-size: 1.2rem;
  line-height: 1.7;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.navBox{
  background-color: rgba(0, 0, 0, 0.055);
  padding:70px 0;
  position: absolute;
  left:5%;
  text-align: left;
  z-index: -1;
}


.navLinks{

justify-content: left;
list-style-type: none;
color: rgb(255, 255, 255);
font-size: 1.2rem;
padding:auto 0;
z-index: -1;
}
.navItem{
 padding: 40% 0; 
 font-size: 1.2rem;
 z-index: -1;
 transition: all .2s ease-in-out;
}
.navItemhover{
  padding: 40% 0; 
  
   transform: scale(1.1); 
}
.intro-header{
  color:white;
  position: relative;
  text-align: center;
  width: 250px;
  margin-bottom: 15px;
}
.header-container{
  text-align: center;
  justify-content: center;
  width: 100%;

  display: flex;
}
.right-container{
  padding-top: 300px;
 
  width: 300px;
  text-align: left;
}
.mid-container{
  margin: auto 60px auto 140px;
  padding-top: 0;
}
.left-container{
  padding-top: 300px;
  padding-left: 50px;
  width: 25px;
  text-align: left;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.startNav{
  opacity: 0;

}


.fadeInNav {
  opacity: 1;
  transition-duration: 1000ms;
}


  .fadeOutNav {
    opacity: 0;
    
    transition-duration: 1000ms;
  }
  


    .startHam{
      opacity: 1;
      transition-duration: 1000ms;
    }
    
    
    .showHam {
      opacity: 1;
      transition-duration: 1000ms;
    }
    

    
      .hideHam {
        opacity: 0;
        transition-duration: 1000ms;
      }
      
    
      .closeBut {
        opacity: 1;
          
          transition-duration: 1000ms;
      }
   
        .closeBut img{
          width:70%;
          padding-top: 70px;
        }

        .fadeOutClose img {
          width:70%;
          padding-top: 70px;
        }
        .fadeOutClose {
          opacity: 0;
          
          transition-duration: 1000ms;
        }

        .secondContainer{
          width:100%;
          padding-top:100px;
          padding-bottom:100px;
          height:auto;
          background-color: #eff0f2;
          opacity: 1;
          
          transition-duration: 1000ms;
        }
        .secondContainerPreScroll{
          width:100%;
          padding-top:100px;
          padding-bottom:100px;
          height:auto;
          background-color: #eff0f2;
          opacity: 0;
          
          transition-duration: 1000ms;
        }
        .secondContainer img{
          width:30%;
        }
        .intro-button{     
          text-decoration: underline;
          color:rgb(93 126 167 / 1);
          padding-top: 50px;
          }

.thirdContainer{
  justify-content: center;
  text-align: center;
  width: 100%;
  position: relative;
  height:auto;
  background-color: white;
  display: flex;
}
.cardObj{
  width:325px;
  height: 550px;
  background-color: white;
  border-color: rgb(209, 209, 209);
  border-style: solid;
  border-width:1px;
  border-radius: 2%;
  display: inline-block;
  margin:50px 30px;
  box-shadow: 5px 10px 20px 1px rgba(0,0,0,0.253)!important;
}
.overflow{
  overflow: hidden;
}
.paraCard{
  font-size:15px;
  padding: 0 17px;
  transform: scale(1);
  transition:transform ease-in-out 0.3s;
}
.paraCard:hover{
  transform: scale(1.05);
  transition:transform ease-in-out 0.3s;
}
.cardTitle{
  margin:20px auto;
  color: #6e7175;
  transform: scale(1);
  transition:transform ease-in-out 0.3s;
}
.cardTitle:hover{
  transform: scale(1.05);
  transition:transform ease-in-out 0.3s;
}

.danceImg{
  width:120%;
  transform: scale(1);
  transition:transform ease-in-out 0.3s;
}
.danceImg:hover{
  width:120%;
  transform:scale(1.05);
  transition:transform ease-in-out 0.3s;
}
.cardButton{
  margin-top: 15px;
  background-color: white;
  color: #88a0da;
  border: 2px solid #88a0da; 
  border-radius: 5px;
  padding:12px 30px;
  font-size: 15px;
  transform: scale(1);
  transition:transform ease-in-out 0.3s;
}
.cardButton:hover{
  transform: scale(1.03);
  transition:transform ease-in-out 0.3s;
}

.fourthContainer{
  justify-content: center;
  width: 100%;
  position: relative;
  height:auto;
  background-color: white;
  display: flex;
  padding: 50px auto;
  padding-bottom: 80px;
}

.leftField{
  width:700px;
  height:400px;
  padding: 20px 80px;
  text-align: right;
  background-color: #eff0f2;
  display: inline-block;
  margin-right: 20px;
  margin: 80px auto;
  box-shadow: 5px 10px 20px 1px rgba(0,0,0,0.253)!important;
  background-image:url(./vidImg1.png) ;
}
.rightField{
  width:700px;
  height:400px;
  padding: 20px 80px;
  text-align: left;
  color:white;
  display: inline-block;
  margin-left: 20px;
  margin: 80px auto;
  box-shadow: 5px 10px 20px 1px rgba(0,0,0,0.253)!important;
  background-image:url(./vidImg2.png) ;
}
.videoButton{
  margin-top: 10px;
  background-color: transparent;
  color: #3e64bd;
  border: 2px solid #3e64bd7c; 
  border-radius: 5px;
  padding:12px 40px;
  font-size: 15px;
  transform: scale(1);
  transition:transform ease-in-out 0.3s;
}
.videoButton:hover{
  background-color: #3e64bd;
  color: white;
  transition:transform ease-in-out 0.5s;
}

.footerSection{
  width:100%;
  height: auto;
  background-color: #1c2024;
  color: rgb(167, 167, 167);
  text-align: center;
  justify-content: center;
  padding:40px auto;
}
.footerSection p{
  font-size: small;
}
.upperFoot{
  display: flex;
  justify-content: center;
  padding-top:30px ;
}
.leftFoot{
  width:300px;
  margin: 0 50px;
}
.midFoot{
  width:300px;
  margin: 0 50px;
}
.rightFoot{
  width:300px;
  margin: 0 50px;
}
.lowerFoot{
  display: flex;
  justify-content: center;
  text-align: center;
}
.lowerFootSec1{
  margin-right: 10%;
}
.lowerFootSec2{
  margin-left: 10%;
}

